












import Vue from 'vue';
// @ts-ignore
import VueChatScroll from 'vue-chat-scroll';

import store from '@/store';
import Index from '@/views/Index.vue';
import { netcommand } from './proto';
import * as netlogin from '@/net/netlogin';

Vue.use(VueChatScroll);

export default Vue.extend({
  name: 'App',
  store,
  components: {
    Index,
  },
  mounted: () => {
    Vue.prototype.$store = store;
  },
  computed: {
    message(): any {
      return this.$store.state.socket.message;
    },
    isConnected(): boolean {
      return this.$store.state.socket.isConnected;
    },
  },
  watch: {
    message(): void {
      // console.log('watch message->', this.message);
    },
    isConnected(): void {
      if (this.isConnected) {
        const { token, gameflag } = this.$route.query;
        if (!token) {
          this.$alert('token', '', {
            confirmButtonText: '',
            callback: (action) => {
              this.$message({
                type: 'info',
                message: `action: ${action}`,
              });
            },
          });
          return;
        }
        const sToken = token as string;
        let sGameFlag = gameflag as string;
        if (!sGameFlag) {
          sGameFlag = '';
        }
        netlogin.C2GSVertify({ sToken, sGameFlag });
      }
    },
  },
});
