




















import Vue from 'vue';
import moment from 'moment';

export default Vue.extend({
  name: 'MessageInput',
  data() {
    return {
      textarea: '',
    };
  },
  computed: {
    disabled(): boolean {
      return this.textarea === '';
    },
  },
  methods: {
    onSubmit() {
      if (this.textarea !== '') {
        const sid = this.$store.state.session.currentSessionId;
        this.$store.dispatch('session/sendMessage', { target: sid, msg: this.textarea });
        this.textarea = '';
      }
    },
  },
});
