









import Vue from 'vue';
import Panel from '@/components/Panel.vue';

export default Vue.extend({
  name: 'Index',
  data() {
    return {
      clientHeight: 1000,
    };
  },
  components: {
    Panel,
  },
  mounted() {
    this.clientHeight = window.innerHeight;
    window.onresize = () => {
      this.clientHeight = window.innerHeight;
    };
  },
});
